___CSS_LOADER_EXPORT___.push([module.id, `/* Set overall page structure */
.page-container {
     height: 100%;
     width: 100%;
     display: grid;
     overflow-y: hidden;
     grid-template-areas:
          "header header"
          "sidebar content";
     grid-template-columns: minmax(300rem, auto) 1fr;
     grid-template-rows: auto 1fr;
}

.header-container {
     padding: 10rem 40rem;
     grid-area: header;
     background-color: #e2e8f0;
     display: grid;
     grid-auto-flow: column;
     grid-template-columns: auto auto;
     justify-content: space-between;
     align-items: center;
}

.header-item {
     height: 40rem;
     border-radius: 5rem;
}

.header-left {
     display: flex;
     gap: 10rem;
}

.header-right {
     display: flex;
     gap: 10rem;
}

.login-button,
.signup-button {
     padding: 8rem 40rem;
     border-radius: 8rem;
}

input {
     margin: 0;
     padding-left: 20rem;
     padding-right: 10rem;
}

.header-left :hover,
.header-right :hover {
     background-color: #cbd5e1;
     cursor: pointer;
}

.searchbar-container {
     height: 30rem;
     border-radius: 5rem;
     margin: 0;
     padding: 0;
     display: flex;
     align-items: center;
}

.searchbar-container input {
     background-color: hsla(0, 9%, 87%, 0.8);
}

.searchbar-container :hover {
     cursor: text;
     background-color: #fafaf9;
}

.searchbar-container :focus {
     width: 400rem;
}

.search-bar {
     height: 30rem;
     align-self: center;
     border: none;
     outline: none;
     border-radius: 5rem;
     position: relative;
}

.search-item {
     position: absolute;
     z-index: 1;
     height: 20rem;
}

.header-left .menu-item {
     position: relative;
}

.sidebar-container {
     display: flex;
     height: 100vh;
     flex-direction: column;
     background-color: #f1f5f9;
     color: #222;
     padding: 10rem;
}

/* .sidebar-container .toggle{
     position: relative;
     left: -300rem;
} */

.sidebar-container .projects {
     margin-top: 40rem;
}

.projects span {
     display: inline-block;
     margin-right: auto;
}

.sidebar-container div {
     display: flex;
     gap: 16rem;
     justify-content: left;
     align-items: center;
     padding: 5rem;
     border-radius: 5rem;
     cursor: pointer;
}

.sidebar-container div:hover,
.sidebar-container div:focus {
     background-color: #ccc;
}

.projects :nth-child(n) {
     height: 20rem;
}

.content-container {
     grid-area: content;
}
